  getInput($event, i, input: any) {
    this.kpiArray[i] = $event.target.value;
    this.validArray[i].filled = input.valid;
    console.log(input.valid);
    if ($event.target.value >= 100) {
      this.validArray[i].number = false;
    } else if (this.validArray[i].number || this.validArray[i].filled) {
      this.disabled = true;
    }
  }

  emitArray() {
    console.log("teste");
    console.log(this.kpiArray);
    this.finalGrade =
      ((this.getMS(this.kpiArray[0]) +
        this.getFDN(this.kpiArray[1]) +
        this.getAmido(this.kpiArray[2]) +
        this.getNdt(this.kpiArray[3]) +
        this.getLeite(this.kpiArray[4]) +
        this.getKPS(this.kpiArray[5])) /
        6) *
      10;
    this.letterGrade = this.score(this.finalGrade);
  }

  log(input) {
    console.log(input);
  }
  score(avg) {
    this.finalGrade = avg;
    avg = Math.round(avg);
    if (avg >= 91) {
      return "A";
    } else if (avg >= 81) {
      return "A-";
    } else if (avg >= 71) {
      return "B";
    } else if (avg >= 61) {
      return "B-";
    } else if (avg >= 51) {
      return "C";
    } else if (avg >= 41) {
      return "C-";
    } else if (avg >= 41) {
      return "D";
    } else if (avg >= 31) {
      return "D-";
    } else if (avg <= 11 && avg >= 10) {
      return "E";
    } else if (avg >= 0 && avg < 10) {
      return "E-";
    } else {
      return "F";
    }
  }
  getMS(value) {
    value = Math.round(value);
    if (value > 40 || value < 20) {
      return 0;
    } else {
      return this.msArray[value - 20];
    }
  }
  getFDN(value) {
    value = Math.round(value);
    if (value <= 35) {
      return 10;
    } else if (value >= 55) {
      return 0;
    } else {
      return this.fdnArray[value - 36];
    }
  }
  getAmido(value) {
    value = Math.round(value);
    if (value < 20) {
      return 0;
    } else if (value >= 40) {
      return 10;
    } else {
      return this.amidoArray[value - 20];
    }
  }
  getNdt(value) {
    value = Math.round(value);
    if (value < 64) {
      return 0;
    } else if (value >= 74) {
      return 10;
    } else {
      return this.ndtArray[value - 64];
    }
  }
  getLeite(value) {
    if (value < 1.25) {
      return 0;
    } else if (value >= 1.65) {
      return 10;
    } else {
      const arr = this.leiteArray.filter((a) => a < value);
      return (arr.length - 1) * 0.5;
    }
  }
  getKPS(value) {
    value = Math.round(value);
    if (value < 50) {
      return 0;
    } else if (value >= 70) {
      return 10;
    } else {
      return this.kpsArray[value - 50];
    }
  }
